import React, { useState } from 'react'
import { Link } from 'react-router-dom'


function LoginPage(props) {

  const sendpass = (e) => {
    e.preventDefault()
    setMsg("Username and password sent to your email")
  }

  const [username, setName] = useState("")
  const [password, setPass] = useState("")
  const [msg, setMsg] = useState("")


  const checkLogin = (e) => {

    e.preventDefault();

    //onsubmit send username and password to server

    let data = { username, password }

    fetch("/login", {
      method: 'POST', // or 'PUT'
      body: JSON.stringify(data), // data can be `string` or {object}!
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(response =>
       response.json())
      .then(response => {
        //if correct then route to new page
        if (response.valid) {
          props.history.push('/ProfilePage');
        } else {
          //if not correct write inncorrect
          setMsg('Invalid username or password')
        }
      }).catch(error => console.error('Error:', error))
  }

  return (
    <div className="wrapperSIGN">
      <form className="log" onSubmit={checkLogin}>
      <h1 className="h1"> Log In Page</h1>
        <input type="text" required placeholder="Username"  onChange={e => setName(e.target.value)} /><br />
        <input type="password" required placeholder="Password" onChange={e => setPass(e.target.value)} /><br />
        <button type="submit">Sign In</button>
        <button style={{background: "green"}}><Link to="/SignUpPage">Sign Up</Link></button>
        <button onClick={sendpass} style={{background:"yellow",width:"fit-content",fontSize:"0.8rem",color:"black",bold:"700"}}>forgot your password?</button>
        <p style={{fontFamily:"Arial Black, Gadget, sans-serif"}}>{msg}</p>
      </form>
     
    </div>
  )
}


export default LoginPage;
