import React, { useState, useEffect } from 'react'



function TeachPage(props) {

  const [course, setCourse] = useState("")
  const [headline, setHeadline] = useState("")
  const [context, setContext] = useState("")
  const [showTeachModal, setShowTeachModal] = useState(false);
  const [showTeachModal2, setShowTeachModal2] = useState(false);
  const [teachobj, setTeachobj] = useState([]);

  useEffect(() => {
    fetch("/onloadteach")
      .then(response => response.json()
        .then(response => {
          //if correct then route to new page
          if (response) {
            setTeachobj(response);
            console.log(teachobj)
          } else {
            //if not correct write inncorrect
            alert("oops")
          }
        }).catch(error => console.error('Error:', error)))
  }, [])


  const newTeachPost = (e) => {
    e.preventDefault();

    let postdata = { course, headline, context };

    fetch("/teach", {
      method: 'POST', // or 'PUT'
      body: JSON.stringify(postdata), // data can be `string` or {object}!
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(response => response.json()
      .then(response => {
        //if correct then route to new page
        if (response) {
          teachobj = response;
          console.log(teachobj)
          props.history.push('/TeachPage');
        } else {
          //if not correct write inncorrect
          alert("oops")
        }
      }).catch(error => console.error('Error:', error)))

  }
  return (
    <div className="wrapperpages">
      <table>
        <thead>
          <tr>
            <th className="date">Date</th>
            <th className="course">Course</th>
            <th className="headline">Headline</th>
            <th className="user">User</th>
          </tr>
        </thead>
        {
          teachobj.map((teachpost, index) => {
            return (
              <tbody id="transData" key={index}>
                <tr>
                  <td className="date"><span className="postPublishDate">{teachpost.date}</span></td>
                  <td className="course"><span className="Coursename">{teachpost.course}</span></td>
                  <td className="headline"><span className="mainHeadline"></span>{teachpost.headline}</td>
                  <td className="user"><span className="PublisherUsername"></span>Belete</td>
                  <td className="user"><span className="PublisherUsername"></span><a className="button2" onClick={() => { setShowTeachModal2(true) }}>Open Post</a></td>
                  {showTeachModal2 ?
                    <div id="popup2" className="overlayModal">
                      <div className="popup">
                        <a className="close" onClick={() => { setShowTeachModal2(false) }}>&times;</a>
                        <div className="content2">
                          <p >{teachpost.context}</p>
                        </div>
                      </div>
                    </div>
                    :
                    <div></div>
                  }
                </tr>
              </tbody>
            )
                }
          )}
      </table>
      <div className="box">
        <a className="button" onClick={() => { setShowTeachModal(true) }}>Add new Post</a>
      </div>
      {showTeachModal ?
        <div id="popup1" className="overlayModal">
          <div className="popup">
            <a className="close" onClick={() => { setShowTeachModal(false) }}>&times;</a>
            <div className="content">
              <form onSubmit={newTeachPost}>
                <input type="text" placeholder="Enter Course name" onChange={e => setCourse(e.target.value)}></input>
                <input type="text" placeholder="Enter Headline" onChange={e => setHeadline(e.target.value)}></input>
                <textarea placeholder="Enter context" className="biggertextbox" onChange={e => setContext(e.target.value)}></textarea>
                <input type="submit" value="Publish"></input>
              </form>
            </div>
          </div>
        </div>
        :
        <div />
      }
    </div >

  )
}

export default TeachPage;