import React from 'react'
import { Link } from 'react-router-dom'


function Homepage() {
    return (
        <div>
            <section className="titles">
                <br />
                <h1>AvivProject </h1>
                <p className="htwo">This website is about helping each other study. via sharing documents and knowlege, asking and offering help.</p>
            </section>

            <section className="container-boxes">
                <div className="box1">
                    <div className="boxtext">
                        <h3><Link to="/TeachPage">Teach</Link></h3>
                        <p>help others by offer teaching the classNamees you are good at.</p>
                    </div>
                </div>

                <div className="box2">
                    <div className="boxtext">
                        <h3><Link to="/LearnPage">Learn</Link></h3>
                        <p>Here you can ask students for help when you dont get the classNamees</p>
                    </div>
                </div>

                <div className="box3">
                    <div className="boxtext">
                        <h3><Link to="/SharePage">Share</Link></h3>
                        <p>Help other succeed with your summaries,articles and answers.</p>
                    </div>
                </div>
            </section>
        </div>


    )
}

export default Homepage;