import React, { useState, useEffect } from 'react'


//--- need to finish profile page fetch--//
function ProfilePage(props) {

  const [fullname, setFullname] = useState(" ");
  const [degree, setDegree] = useState(" ");
  const [institute, setInstitute] = useState(" ");
  const [phone, setPhone] = useState(" ");
  const [descript, setDescript] = useState(" ");
  const [showProfileModal, setShowProfileModal] = useState(false);
  const [infobj, setInfobj] = useState([]);

  useEffect(() => {
    fetch("http:/onloadprofile")
      .then(response => response.json()
        .then(response => {
          //if correct then route to new page
          if (response) {
            setInfobj(response);
            console.log(infobj)
          } else {
            //if not correct write inncorrect
            alert("oops")
          }
        }).catch(error => console.error('Error:', error)))
  }, [])

  const profileInfo = (e) => {
    e.preventDefault();

    let postprofileInfo = { fullname, degree, institute, phone, descript };

    fetch("http:/profileInfo", {
      method: 'POST', // or 'PUT'
      body: JSON.stringify(postprofileInfo), // data can be `string` or {object}!
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(response => response.json()
      .then(response => {
        //if correct then route to new page
        if (response) {
          setInfobj(response);
          console.log(infobj)
          props.history.push('/Profilepage');
        } else {
          //if not correct write inncorrect
          alert("oops")
        }
      }).catch(error => console.error('Error:', error)))
  }

  return (
    <div className="profilecontainer">
      <div className="profileoverlay">
        <div className="profileuser"></div>
        <div className="profilehead">
          <p>Name:{fullname}<br />Degree:{degree}<br />Institute:{institute}</p>
          <p></p>
          <hr />
        </div>
        <div className="userdescription">
          <p className="profiledescription">{descript}<br />Phone:{phone}</p>
        </div>
        <div className="profiledit"><button onClick={() => { setShowProfileModal(true) }}>edit your profile</button></div>
      </div>
      {showProfileModal ?
        <div id="popup2" className="overlayModalProfile">
          <div className="editprofilecontainer">
            <a className="close" style={{ fontSize: "30px", fontWeight: "bolder" }} onClick={() => { setShowProfileModal(false) }}>&times;</a>
            <form className="editprofilecontact" onSubmit={profileInfo}>
              <h3>User Proflie</h3>
              <h4>Share your information</h4>
              <input placeholder="Your full name" type="text" tabIndex="1" required autoFocus onChange={e => setFullname(e.target.value)} />
              <br />
              <input placeholder="Your Institute" type="text" tabIndex="2" required onChange={e => setInstitute(e.target.value)} />
              <br />
              <input placeholder="Your Degree" type="text" tabIndex="3" required onChange={e => setDegree(e.target.value)} />
              <br />
              <input placeholder="Your Phone Number (optional)" type="tel" tabIndex="4" required onChange={e => setPhone(e.target.value)} />
              <br />
              <textarea style={{ resize: "none", overflow: "hidden" }} tabIndex="5" required onChange={e => setDescript(e.target.value)} > </textarea>
              <br />
              <button name="submit" type="submit" className="editprofilecontact-submit" onClick={() => { setShowProfileModal(false) }}>Submit</button>
            </form>
          </div>

        </div>
        :
        <div></div>
      }
    </div>
  )
    }


export default ProfilePage;
