import React from 'react'
import './header.css';
import { Link } from 'react-router-dom'


function Header() {
  return (
    <header>
      <div>
        <p>AvivProject</p>
      </div>
      <nav>
        <ul>
          <li><Link to="/">Home</Link></li>          
          <li><Link to="/ProfilePage">Profile</Link></li>
          <li> <Link to="/LoginPage">Log-In</Link></li>
        </ul>
      </nav>
    </header>
  )
}

export default Header 