import React, { useState, useEffect } from 'react'



function LearnPage(props) {

  const [course, setCourse] = useState("")
  const [headline, setHeadline] = useState("")
  const [context, setContext] = useState("")
  const [showLearnModal, setShowLearnLearnModal] = useState(false);
  const [showLearnModal2, setShowLearnLearnModal2] = useState(false);
  const [learnobj, setLearnobj] = useState([]);

  useEffect(() => {
    fetch("/onloadlearn")
      .then(response => response.json()
        .then(response => {
          //if correct then route to new page
          if (response) {
            setLearnobj(response);
            console.log(learnobj)
          } else {
            //if not correct write inncorrect
            alert("oops")
          }
        }).catch(error => console.error('Error:', error)))
  }, [])


  const newLearnPost = (e) => {
    e.preventDefault();

    let postdata = { course, headline, context };

    fetch("/learn", {
      method: 'POST', // or 'PUT'
      body: JSON.stringify(postdata), // data can be `string` or {object}!
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(response => response.json()
      .then(response => {
        //if correct then route to new page
        if (response) {
          setLearnobj(response);
          console.log(learnobj)
          props.history.push('/LearnPage');
        } else {
          //if not correct write inncorrect
          alert("oops")
        }
      }).catch(error => console.error('Error:', error)))

  }
  return (
    <div className="wrapperpages">
      <table>
        <thead>
          <tr>
            <th className="date">Date</th>
            <th className="course">Course</th>
            <th className="headline">Headline</th>
            <th className="user">User</th>
          </tr>
        </thead>
        {
          learnobj.map((learnpost, index) => {
            return (
              <tbody id="transData" key={index}>
                <tr>
                  <td className="date"><span className="postPublishDate">{learnpost.date}</span></td>
                  <td className="course"><span className="Coursename">{learnpost.course}</span></td>
                  <td className="headline"><span className="mainHeadline"></span>{learnpost.headline}</td>
                  <td className="user"><span className="PublisherUsername"></span>Michael</td>
                  <td className="user"><span className="PublisherUsername"></span><a className="button2" onClick={() => { setShowLearnLearnModal2(true) }}>Open Post</a></td>
                  {showLearnModal2 ?
                    <div id="popup2" className="overlayModal">
                      <div className="popup">
                        <a className="close"  onClick={() => { setShowLearnLearnModal2(false) }}>&times;</a>
                        <div className="content2">
                          <p >{learnpost.context}</p>
                        </div>
                      </div>
                    </div>
                    :
                   <div></div>
                  }
                </tr>
              </tbody>
            )
          }
          )}
      </table>
      <div className="box">
        <a className="button" onClick={() => { setShowLearnLearnModal(true) }}>Add new Post</a>
      </div>
      {showLearnModal ?
        <div id="popup1" className="overlayModal">
          <div className="popup">
            <a className="close" onClick={() => { setShowLearnLearnModal(false) }}>&times;</a>
            <div className="content">
              <form onSubmit={newLearnPost}>
                <input
                  type="text"
                  placeholder="Enter Course name"
                  onChange={e => setCourse(e.target.value)}>
                </input>
                <input type="text" placeholder="Enter Headline" onChange={e => setHeadline(e.target.value)}></input>
                <textarea
                  laceholder="Enter context"
                  className="biggertextbox"
                  onChange={e => setContext(e.target.value)}></textarea>
                <input type="submit" value="Publish"></input>
              </form>
            </div>
          </div>
        </div>
        :
        <div />
      }
    </div >

  )
}

export default LearnPage;