import React, { useState } from 'react'
  
function SignUpPage(props) {
  const [username, setUserame] = useState("")
  const [password, setPassword] = useState("")
  const [email, setEmail] = useState("")
  const [msg, setMsg] = useState("")

  const registerfunc = (e) => {
    e.preventDefault()
    console.dir(`inserted username:${username}. inserted password:${password}. inserted email:${email}`)
    // if (password.length < 8) { setMsg("Password is too short (8digits at least)") }
    // if (username.length > 25) { setMsg("Username is too long") }


    //onsubmit send username and password to server

    let regdata = { username, password, email }

    fetch("http:/register", {
      method: 'POST', // or 'PUT'
      body: JSON.stringify(regdata), // data can be `string` or {object}!
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(res => res.json())
      .then(response => {
        //if correct then route to new page
        if (response.valid) {
          alert("new user added")
          props.history.push('/LoginPage');
        } else {
          //if not correct write inncorrect
          setMsg('Username or Email are already in use')
        }
      }).catch(error => console.error('Error:', error))
  }

  return (
    <div className="wrapperSIGN">
    <form className="log" onSubmit={registerfunc}>
    <h1 className="h1">Sign Up Page </h1>
      <input style={{margin:"15px -4px"}} type="text" required placeholder="Username"  onChange={e => setUserame(e.target.value)} />
      <input style={{margin:"15px -4px"}} type="password" required placeholder="Password" onChange={e => setPassword(e.target.value)} />
      <input style={{margin:"15px -4px"}} type="text" required placeholder="Email" onChange={e=> setEmail(e.target.value)}/>
      <button type="submit">Sign Up</button>
      <p style={{fontFamily:"Arial Black, Gadget, sans-serif"}}>{msg}</p>
    </form>
   
  </div>

  )
}
export default SignUpPage;     