import React, { useState,useEffect } from 'react'



function SharePage(props) {

  const [course, setCourse] = useState("")
  const [headline, setHeadline] = useState("")
  const [context, setContext] = useState("")
  const [showTeachModal, setShowShareModal] = useState(false);
  const [showTeachModal2, setShowShareModal2] = useState(false);
  const [shareobj, setSharechobj] = useState([]);

  useEffect(() => {
    fetch("/onloadshare")
      .then(response => response.json()
        .then(response => {
          //if correct then route to new page
          if (response) {
            setSharechobj(response);
            console.log(shareobj)
          } else {
            //if not correct write inncorrect
            alert("oops")
          }
        }).catch(error => console.error('Error:', error)))
  }, [])


  const newSharePost = (e) => {
    e.preventDefault();

    let postdata = { course, headline, context};

    fetch("/share", {
      method: 'POST', // or 'PUT'
      body: JSON.stringify(postdata), // data can be `string` or {object}!
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(response => response.json()
      .then(response => {
        //if correct then route to new page
        if (response) {
          setSharechobj(response);
          console.log(shareobj)
          props.history.push('/SharePage');
        } else {
          //if not correct write inncorrect
          alert("oops")
        }
      }).catch(error => console.error('Error:', error)))

  }
  return (
    <div className="wrapperpages">
      <table>
        <thead>
          <tr>
            <th className="date">Date</th>
            <th className="course">Course</th>
            <th className="headline">Headline</th>
            <th className="user">User</th>
          </tr>
        </thead>
        {
          shareobj.map((sharepost, index) => {
            return (
              <tbody id="transData" key={index}>
                <tr>
                  <td className="date"><span className="postPublishDate">{sharepost.date}</span></td>
                  <td className="course"><span className="Coursename">{sharepost.course}</span></td>
                  <td className="headline"><span className="mainHeadline"></span>{sharepost.headline}</td>
                  <td className="user"><span className="PublisherUsername"></span>Belete</td>
                  <td className="user"><span className="PublisherUsername"></span><a className="button2"onClick={() => { setShowShareModal2(true) }}>Open Post</a><div id="popup2" className="overlayy"></div></td>
                  {showTeachModal2 ?
                    <div id="popup2" className="overlayModal">
                      <div className="popup">
                        <a className="close" onClick={() => { setShowShareModal2(false) }}>&times;</a>
                        <div className="content2">
                          <p >{sharepost.context}</p>
                        </div>
                      </div>
                    </div>
                    :
                    <div></div>
                  }
                </tr>
              </tbody>
            )
          }
          )}
      </table>
      <div className="box">
        <a className="button" onClick={() => { setShowShareModal(true) }}>Add new Post</a>
      </div>
{showTeachModal ?
      <div id="popup1" className="overlayModal">
        <div className="popup">
          <a className="close" onClick={() => { setShowShareModal(false) }}>&times;</a>
          <div className="content">
            <form onSubmit={newSharePost}>
              <input type="text" placeholder="Enter Course name" onChange={e => setCourse(e.target.value)}></input>
              <input type="text" placeholder="Enter File Topic" onChange={e => setHeadline(e.target.value)}></input>
              <label htmlFor=" Year">File Type</label>
              <select id=" year" name="year">
                <option value="one">exam</option>
                <option value="two">essay</option>
                <option value="three">shlif</option>
              </select><br />
              <textarea placeholder="Enter context" className="biggertextbox" onChange={e => setContext(e.target.value)}></textarea>
              <input type="submit"  value="Publish"></input>
              </form>
            </div>
          </div>
        </div>
        :
        <div />
      }
    </div >

  )
}

export default SharePage;